  position: fixed;
  z-index: 5001;
  width: 7.6em;
  min-height: 7.6em;
  top: 2.4rem;
  left: 50%;
  margin-left: -3.8em;
  background: rgba(17, 17, 17, 0.7);
  text-align: center;
  border-radius: 0.066667rem;
  color: #FFFFFF;
}
